import { Component, OnInit } from '@angular/core';
import { ApiApplicationService } from 'src/app/services/api-application.service';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-nav-steps',
  templateUrl: './nav-steps.component.html',
  styleUrls: ['./nav-steps.component.scss'],
})
export class NavStepsComponent implements OnInit {

  arrSteps = {'academicDetails':null, 'documentsUpload':null, 'payment':null,'personalDetails':null,'serviceSelection':null, 'universityDetails':null};  
  applicationId = 0;
  arrAppUniversities = {'universityName':'', 'countryName':''};
  constructor(
    private apiApplicationService : ApiApplicationService,private auth: AuthService
  ) { }

  ngOnInit() {
    this.getApplicatinID();      
  }
  GetApplicationSteps(){
    this.apiApplicationService.getApplicationSteps().then(resp => {
      this.arrSteps = resp;
    }).catch(err => {
      if(err.error.error == "invalid_token"){
        //this.auth.logout();
      }
    })
  }
  async getApplicatinID() {
    await this.auth.getApplicationID().then(res => {
      this.applicationId = parseInt(res);
      if(this.applicationId != 0){
        this.GetApplicationSteps();
        this.getAppUniverisityDetails();
      }
    });
  }
  getAppUniverisityDetails() {
    let postData = '{"ApplicationId":' + this.applicationId + ',"IsActive":true }';
    this.apiApplicationService.getAppUniversity(postData).then(resp => {
      if (resp.length > 0) {
        this.arrAppUniversities = resp[0];        
      }
    })
  }
}
