import { ApiGeneralService } from './../../services/api-general.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';

@Component({
  selector: 'app-top-links',
  templateUrl: './top-links.component.html',
  styleUrls: ['./top-links.component.scss'],
})
export class TopLinksComponent implements OnInit {
  @ViewChild('slides', { static: true }) slides: IonSlides;
  slideOpts = {
    slidesPerView: 4.5,
    freeMode: true,
    initialSlide: 5
  }
  constructor(private apiGeneralService: ApiGeneralService) { }
  ngOnInit() {}
  ngAfterViewInit() {
    this.slides.slideTo(this.apiGeneralService.sliderTopLinksIndex+1, 0);
  }
  slideChanged() {
    this.slides.getActiveIndex().then((index: number) => {
      this.apiGeneralService.sliderTopLinksIndex = index;
    });
  }
}
