import { Router } from '@angular/router';
import { StorageService } from './../../services/storage.service';
import { ApiGeneralService } from './../../services/api-general.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-top-links-predictor',
  templateUrl: './top-links-predictor.component.html',
  styleUrls: ['./top-links-predictor.component.scss'],
})
export class TopLinksPredictorComponent implements OnInit {

  @ViewChild('slidesPredictor', { static: true }) slides: IonSlides;
  
  slideOpts = {
    slidesPerView: 3.8,
    freeMode: false,
    initialSlide: 7
  }

  imgPath = environment.webAssetsPath + "mobility-app";
  arrPredictedForm:any;
  arrActivatedpackage = null;

  examId = null;
  counsellingId = null;
  counsellingIdOther = null;

  isModal = false;

  packageName = this.apiGeneralService.optedPackage;
  packageId = 0;
  selectedURL = "";

  constructor(private apiGeneralService: ApiGeneralService, private storage : StorageService, private router : Router) { 
    this.getredictedForm();    
  }  
  ngOnInit() {
    this.getSelectedPackage();
  }
  ngAfterViewInit() {    
    this.slides.slideTo(this.apiGeneralService.sliderTopLinksPredIndex, 0);
  }
  slideChanged() {
    this.slides.getActiveIndex().then((index: number) => {
      this.apiGeneralService.sliderTopLinksPredIndex = index;
    });
  }
  getredictedForm(){
    this.storage.getObject("predictorForm").then(resp=>{
      this.arrPredictedForm = resp;
      this.examId = this.arrPredictedForm.ExamID;
      this.counsellingId = this.arrPredictedForm.CounsellingID;
      if(this.counsellingId == 1){
        this.counsellingIdOther = 2
      }
      else if(this.counsellingId == 2){
        this.counsellingIdOther = 1
      }      
    })
  }
  getSelectedPackage(){
    this.storage.getObject("selectedPackage").then((resp:any)=>{      
      this.apiGeneralService.optedPackage = resp["packageName"];
      this.packageName = resp["packageName"];
      this.packageId = resp["packageId"];
    })
  }

  //#region Modal Start
  onClickUpgrade(packageId, urlRouter, urlID){
    if(packageId < 4){
      this.router.navigate(["/home/college-predictor/predictor-checkout", 'upgrade']);
    }
    else{      
      this.router.navigate([urlRouter]);
      this.selectedURL = urlID;
    }
  }
  onClickModalClose() {
    this.isModal = false;
  }
  //#endregion

}
